.work_div{
    padding-bottom: 100px;
    margin-left: 60px;
    position: relative;
    background: #ecb4c1 url("../img/works_bg.jpg") center top no-repeat;
    background-size: 100%;
}

.work_div .title{
    -webkit-text-stroke: 3px #d75871;
    font-size: 91px;
    font-weight: 800;
    width: 100%;
    padding: 58px 0;
    color: white;
    text-align: center;

}
.works_wrap {
    width: 1200px;
    margin: 50px auto;
    display: flex; justify-content: end; align-items: end;
    flex-direction: column;
}
.box {
    width: 600px; height: 300px;
    margin: auto; margin-bottom: 43px;
    position: relative;
    transform-style: preserve-3d;
    right: 0;
    top: 10%;
    width: 100px;
    transition: 1s;
    transform: rotateX(9deg) rotateY(-7deg) rotateZ(1deg) translate(-70px, 0);

}
.box:last-child{ margin-bottom: 30px;}

.cube {
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 200px; height: 200px;
    border:none;
}
  

.box .back{
    width: 600px; height: 300px;
    background: rgba(241, 241, 241, 0.8);
    transform: translateZ(-100px);
    padding-right: 39px;
}
.box .back p{
    text-align: right;
}
.box .left{
    height: 300px;
    background: rgba(241, 241, 241, 0.6);
    transform: translateX(-100px) rotateY(90deg);
}
.box .right{
    height: 300px;
    background: rgba(241, 241, 241, 0.6);
    transform: translateX(500px) rotateY(+90deg);
}
.box .bottom{
    width: 600px; height: 200px;
    background: rgba(241, 241, 241, 0.6);
   transform: translateY(200px) rotateX(90deg);
}
.box .top{
    width: 600px; height: 200px;
    background: rgba(241, 241, 241, 0.6);
   transform: translateY(-100px) rotateX(90deg);
}
.box .front{
    cursor: pointer;
    background: #d6cec3;
    width: 600px; height: 300px;
    transform: translateZ(100px);
}
.box .front a{
    display: block;
    width: 100%;
    height: 300px;
}
