.detail_popup{
    background: #fef6f4;
    margin-left: 60px;
}
.detail_popup .back {
    position: fixed; left: 60px; top:50%; transform: translateY(-50%);
    display: flex; justify-content: center; align-items: center;
    width: 60px; height: 60px;
    cursor: pointer;

}

