.detail_div{
    width: 100%;
    /* height: 100vh; */
}




.detail_wrap{
    width: 100%;
    padding-top: 30px;
}

.detail_title{
    width: 1200px;
    height: 430px;
    margin: 0 auto 60px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.detail_title .title_img{width: 135%;position: relative;}
.detail_title .monitor{
    width: 100%; height: 430px;
    background: url('../img/monitor.png') center center no-repeat;
    background-size: contain;
    position: relative;
}
.detail_title .monitor_img{
    background: red;
    width: 476px; height: 270px;
    position: absolute; top: 19px; left: 50%; transform: translateX(-50%);

}
.detail_title .phone {
    position: absolute; bottom: -59px; right:-39px ;
    width: 183px;
    height: 400px;
    background: url("../img/phone.png") left top no-repeat;
    background-size: contain;
}
.detail_title .phone_img{
    width: 134px; height: 239px;
    position: absolute; top: 61px; left: 24px;
    background: green;
}
.detail_title .title_sub{
    width: 100%;
}
.detail_title .title_sub p{
    width: 100%;
    text-align: right;
    margin-top: -110px;
    font-size: 80px;font-weight: 800;
}
.detail_title .title_sub span{
    background: #ecb4c1;
    margin-left: 47px; margin-top: 20px;
    padding: 4px 10px 4px 0 ;
    cursor: pointer;
    font-size: 23px;
    text-align: right;
    display: block;
}
.detail_intro {
    width: 1200px;
    margin: 20px auto;
}
.detail_intro p  {margin: 10px 0;}
.detail_intro > p:first-child{
    font-size: 21px;
    font-family: 'pretendard';
    text-align: end;
}
.detail_intro > p:last-child{
    font-size: 21px;
    text-align: end;
}
.detail_sub{
    width: 1200px;
    margin: 0 auto;
    perspective: 6000px;
}
.features_wrap{
    width: 100%;
    /* height: 300px; */
    /* border: 1px solid black; */
    border-radius: 25px;
    position:relative;
    margin: 30px 0 132px;
    transform-style: preserve-3d;
   transition: .5s; 
}
/* .features_wrap:hover{
    transform: rotateY(180deg);
} */
.feat_front{
    position: absolute;
    left: 0; top: 0;
    width: 100%;
    height: 450px;
    backface-visibility:hidden;
    background: url('../img/detail_box.jpg') center top/ cover no-repeat;
    /* border: 1px solid black;  */
    border-radius: 100px 8px 100px 8px;
    display: flex; justify-content: center; align-items: center;
    flex-direction: column;
}
.feat_front p{
    margin: 10px 0; 
    font-size: 33px; font-weight: 700;
    color: #2e2e2e;
}
.feat_front p:last-child{font-weight: 'pretendard';}
.feat_back{
    position: absolute;
    left: 0; top: 0;
    width: 100%;
    height: 450px;
    transform: rotateY(180deg);
	transform-origin: 50% 50% 0px;
    backface-visibility:hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    
}
.feat_back p{
    font-size: 27px; 
    line-height: 1.6;
    margin: 7px 0; width: 80%; 
    text-align: right;
    word-break: keep-all;
}
.feat_back p:nth-of-type(even){font-family: 'pretendard';font-weight: 100;}
.feat_back .back_left{border: 1px solid black; border-radius: 23px;}
.feat_back .back_mid{
    border: 1px solid black; border-radius: 23px;
    margin-left: 24px;
}
.feat_back .back_right{
    width: 300px;
    display: flex; align-items: flex-end;
    flex-direction: column;
}

