.header{
  z-index: 30;
  position: relative;
  width: 60px; height: 100vh;
  background: #202020;
  position: fixed; left: 0; top: 0;
}
.header_div{
  width: 80%; margin: 10px auto;
}
.logo{
  width: 90%;
  display: block; margin: 0 auto;
}
.nav{
  margin: 50px 0;
  width: 100%;
}
.nav .mainMenu { position: relative;}
.nav a{
  display: flex; justify-content: center; align-items: center;
  margin: 30px 0;
}
.nav a .material-icons{
  width: 100%;
  color: white;
}
.nav  .menuName{
  position: absolute;
  top: 50%; right: 0%; transform: translate(119px, -50%);
  display: block;
  background: #535353;
  color: white;
  width: 114px;
  font-size: 23px;
  line-height: 2;
  padding-left: 16px;
  border-radius: 30px 10px 10px 32px;
}

.side{
  position: absolute;
  bottom: 0; left: 0;
  width: 100%; margin: 10px auto;
}
.side p {
  display: flex; justify-content: center; align-items: center;
  width: 100%;
  margin: 15px 0;
  cursor: pointer;
}
.side p img {
  width: 50%;
}
.side p .material-icons {
  color: white;
}
.side > p:last-child{position: relative; cursor: default;}
.side .mail{
  position: absolute;
  top: -50%; right: 0%; transform: translate(272px, 0%);
  display: block;
  background: #fef01b;
  color: black;
  width: 273px;
  font-size: 20px; font-weight: 800;
  line-height: 2;
  padding-left: 16px;
  cursor: default;
}