.cont_div{
    margin-left: 60px ;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.cont_div canvas { 
    position: absolute; top: 0; left: 0;
    width: 100%; height: 100vh; 
}
.cont_div canvas.none {display: none;}

.cont_wrap{
    width: 1200px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.cont_left{
    width: 100%;
    display: flex; justify-content: center; align-items: center;
    flex-direction: column;
}
.cont_left .left_1st{
    font-size: 57px; font-weight: 800;
}
.cont_left .left_2nd{
    margin: 15px 0;
    font-size: 20px;
}
.cont_left .left_img{}
.cont_left .left_img .particle{
    width: 10px;
    height: 10px;
    background: red;
}


.cont_right{
    width: 100%; 
    padding: 0 52px;
}
.cont_right >p {
    width: 100%;
    display: flex;
    margin: 30px 0;
    padding: 10px 19px;
    background: #ecb4c1;

}
.cont_right label{font-size: 21px;}
.cont_right input{
    border: none;
    background: #ecb4c1;
    width: 100%;
    padding: 0 10px;
    outline: none;
}
.cont_right .cont_msg{
    flex-direction: column;
}
.cont_right .cont_msg textarea {
    background: #ecb4c1;
    border: none;
    height: 150px;
    outline: none;
    resize: none;
}

.cont_btn{
    cursor: pointer;
    padding: 10px 28px;
    border: none;
    background: #ecb4c1;
    font-size: 20px;

}