.game_div{
    background : url('../img/game_bg.jpg') center center / cover no-repeat;
    height: 100vh;
    padding-top: 15px;
    margin-left: 60px;
}
.game_div .game_title{
    font-size: 38px;
    font-weight: 800;
    text-align: center;
    margin-bottom: 23px;
}
.game_div .game_sub{
    text-align: center;
    font-family: 'pretendard'; font-size: 19px; font-weight: 100;
    line-height: 1.6;
    margin-bottom: 30px;
}
.game_div .select{
    width: 1200px;
    margin: 0 auto;
}
.sel_wrap{
    margin: 14px 0;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.sel_wrap  > div{
    background: white;
    display: flex; justify-content: center; align-items: center;
    font-size: 18px; font-family: 'pretendard'; font-weight: 100;
    text-align: center;
    line-height: 1.6;
}
.sel_wrap .pink{
    width: 500px;
    height: 105px;
    margin: 0 20px;
    border-radius: 0px 30px 30px 80px;
    border: 8px solid #f48ba9;
    cursor: pointer;
}
.sel_wrap .pink.active{
    background:#f48ba9;
}
.sel_wrap .blue{
    width: 500px;
    height: 105px;
    margin: 0 20px;
    border-radius: 30px 80px 0px 30px;
    border: 8px solid #86cbed;
    cursor: pointer;
}
.sel_wrap .blue.active{
    background: #86cbed;
}

.gam_submit{
    width: 138px;
    padding: 12px 0;
    margin: 23px auto 0;
    font-size: 24px;
    text-align: center;
    background: #ded6cc;
    border-radius: 10px;
    cursor: pointer;
}
/* .game_div{
    background: white;
    height: 100vh;
} */
.game_div .result{
    position: fixed;
    height: 100vh;
    width: 100%;
    margin-left: 60px;
    left: 0; top: 0;
    background: rgb(119, 118, 118,0.7);
    display: none;
} 
.game_div .result.active{
    display: block;
}
.game_div .result > div:first-child canvas{
    width: 1000px; height: 78vh;
}

.game_div .result_wrap{
    width: 1000px;
    height: 78vh;
    position: fixed;
    left: 50%; top: 50%; 
    transform: translate(-50%,-50%);
    display: flex; justify-content: center; align-items: center;
    flex-direction: column;
}
.game_div .result_img{
    width: 200px; height: 200px;
    background: url("../img/game_result.jpg") center center no-repeat;
    background-size: 122%;
    border-radius: 51px 15px 51px 15px;
}
.game_div .result_wrap .employ{
    font-family: 'pretendard';
    color: white;
    width: 80%;
    margin: 26px 0;
    line-height: 2;
    display: flex; justify-content: center; align-items: center; 
    flex-direction: column;
}
.game_div .result_wrap .employ > p{
    font-family: 'pretendard'; font-weight: 100; line-height: 2;
}
.game_div .result_wrap .employ > p:first-child{font-size: 20px;}
.game_div .result_wrap .employ .name { font-size: 22px; font-weight: 700;}
.game_div .result_wrap .btn{
    display: flex;
    justify-content: center;
    align-items: center;
}
.game_div .result_wrap .btn > div{
    cursor: pointer;
    margin: 0 20px;
    height: 33px;
    background: rgb(204, 202, 202);
    display: flex; justify-content: center; align-items: center;
    border-radius: 10px;
    font-size: 17px; font-weight: 700;
}
.game_div .result_wrap .btn .retry{
    width: 87px;
}
.game_div .result_wrap .btn .connect{
    background: rgb(204, 202, 202);
    width: 300px;
    
}