.card_wrap{
    /* margin: 0 auto; */
    /* width: 1080px; */
    width:100%;
    margin-left: 60px;
    height: 100vh;
    background: url("../img/main_bg.jpg") center center no-repeat;
    background-size: cover;
    position: fixed;

}
.card_wrap  .card_box{
    height: 100vh;
    margin-left: 60px;
    position: fixed;
    top: 50%; left: 50%;
    transform: translate(-50%, -50%);
    perspective: 577px;
    perspective-origin: 50% 50%;
}
.card{
    display: inline-block;
    width: 650px; height: 40vh;
    margin: 30vh 215px;
    box-shadow: 12px 8px 2px rgb(54, 54, 54);
    /* background: grey; */
    
    /* background: url("../img/main/card1_bg.png") no-repeat center center; */
}
