}
.card .left p { color: white;}

.card1{
    box-shadow: none;
    /* background: url("../img/logo.png") center center no-repeat; */
    background-size:contain;
    opacity: 0.8;
}
.card1 .left{background: white; border-radius: 233px;}
.card1 p{opacity: 1;}
.card1 p:first-child{
    font-weight: 600;
    font-size: 55px;
    transform: translateY(-123px);
    text-shadow: 0px 6px 0 rgb(58, 57, 57);
    -webkit-text-stroke: 1px black;
}
.card1 p:last-child{
    margin-top:32px;
    font-size: 26px;
    line-height: 1.5; 
    text-shadow: 1px 1px 0.2px white; 
    color: black;
    font-weight: 700;
}
.card1 p:last-child span {
    display: block;
    animation: moving 1s alternate infinite linear;
    font-size: 25px;
}
@keyframes moving {
    0%{transform: translateY(0);}
    100%{transform: translateY(20px);}
    
}
.card2{
    background: url("../img/main_card2.jpg") center center / cover no-repeat;
    border-radius: 23px;
}
.card2 p:first-child{
    font-family: 'pretendard';
    font-size: 46px;
    font-weight: 700;
    transform: translateY(-31px);
    text-align: left;
    text-shadow: 4px 4px 2px rgb(63, 62, 62);
}
.card2 p:last-child{
    display: block;
    font-family: 'pretendard';
    font-size: 24px;
    line-height: 1.5;
    color: black;
    padding: 0 66px;
    text-align: left;
    word-break: keep-all;
}
.card2 p > span:nth-of-type(1) {padding-left: 51px;}
.card2 p > span:nth-of-type(2) {padding-left: 119px;}
.card2 p > span:nth-of-type(3) {
    text-align: center;
    margin-left: 150px;
    animation: moving 1s alternate infinite linear;
}

.card3 {box-shadow: none;}
.card3 .left > p:first-child {
    color: #2e2e2e;
    font-size: 45px; font-weight: 700;
    transform: translateY(-88px);
    /* -webkit-text-stroke: 1px black; */
}
.card3 .left > p:last-child{
    width: 100%;
    height: 30vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.card3 .left > p:last-child >p {
    margin: 0 10px;
    width: 100%;height: 40vh;
    border: 3px solid black;
    box-shadow: 8px 8px 2px rgb(54, 54, 54);
    border-radius: 50px 5px 50px 5px;
}
.card3 .left > p:last-child >p:first-child {
    background: url("../img/main_intro3.jpg") center center no-repeat;
    background-size: cover;
}
.card3 .left > p:last-child >p:last-child {
    background: url("../img/main_intro3-1.jpg") center center no-repeat;
    background-size: cover;
}
.card4{box-shadow: none;}
.card4 .left{
    width: 304px;
    height: 538px;
    background-color: #2e2e2e;
    border-radius: 20px;
    padding: 14px;
    position: relative;    
    margin: 0 auto;

}
.card4 .left::after{
    content: "";
    background: #2e2e2e;
    position: absolute;
    right: -4px;
    top: 36px;
    width: 6px;
    height: 60px;
    border-top-right-radius: 3px;
    border-bottom-right-radius: 3px;
}
.card4 .left::before{
    content: "";
    border: 1px solid white;
    background: transparent;
    position: absolute;
    left: 50%; transform: translateX(-50%);
    bottom: 4px;
    width: 30px;
    height: 30px;
    border-radius: 20px;
}
.card4 .left > p:first-child{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -53%);
    width: 285px;
    height: 485px;
    background: white;
    border-radius: 12px;
    background:url("../img/main_intro4.gif") center top no-repeat;
    background-size: cover;
}

.card4 .left > p:last-child{
    font-size: 25px;
    transform: translateY(-141px);
    color: #2e2e2e;
    
}
.card4 .left > p:last-child > span{
    font-weight: 700;
    font-size: 45px;
    color: transparent;
    -webkit-text-stroke: 3px #2e2e2e;
}

